.product__excerpt {
  p {
    @extend %h3;
  }
}

.product__image {
  margin-bottom: 2rem;
  @include bp-xs {
    width: 60%;
    margin: 0 auto;
  }
  @include bp-sm {
    width: 50%;
    margin: 0 auto 0 0;
  };
  @include bp-md {
    width: 30%;
    margin: 0 auto;
  }
}

@include bp-sm {
  @include lt-bp-md {
    .product__notes  {
      width: 50%;
      position: absolute;
      top: 20%;
      right: 0;
    }
  }
};

.product__note {
  // @extend %text-center;
  width: 100%;
  margin-bottom: 3rem;
  .product__note-heading {
    position: relative;
  }
  @include bp-xs {
    text-align: center;
    width: 70%;
    margin: 0 15% 3rem;
  }
  @include bp-sm {
    text-align: left;
    margin-bottom: 3rem;

    @include lt-bp-md {
      &.product__note--tasting {
        top: 20%;
        right: 0;
      }

      &.product__note--aging {
        top: 45%;
        right: 0;
      }

      &.product__note--composition {
        top: 70%;
        right: 0;
      }
    };

  }
  @include bp-md {
    position: absolute;
    width: 33%;
    max-width: 270px;
    margin: 0;

    &.product__note--tasting {
      top: 20%;
      left: 0;
    }

    &.product__note--aging {
      top: 45%;
      right: 0;
    }

    &.product__note--composition {
      top: 70%;
      left: 0;
    }
  }
}

.product__note {
  .line {
    display: none;
    position: absolute;
    @include bp-md {
      display: block;
      width: 150px;
      top: 1rem;
      left: -175px;
    }
  }
}

@include bp-sm {

  @include lt-bp-md {
    // ipad portrait only

    .product__note {
      .product__note-heading:before, .product__note-heading:after {
        position: absolute;
        display: inline-block;
        top: 0.5em;
      }
    }

    .product__note {
      .product__note-heading:before {
          @include stroke(60px);
          transform: translate3d(calc(-100% - 2rem),0,0);
        }
      }
  };
}

@include bp-md {
  .product__note--tasting, .product__note--composition {
    .line {
      width: 250px;
      left: 80%;
      top: 1rem;
    }
  }
  .product__note--composition {
    .line {
      width: 200px;
    }
  }
}

