.product-colour {
    &-black {
        .product__excerpt p,
        .product__profile-header h2 {
            color: $black;
        }

        strong {
            @include underline('black');
        }
    }

    &-blue {
        .product__excerpt p,
        .product__profile-header h2 {
            color: $blue;
        }

        strong {
            @include underline('blue');
        }
    }

    &-gold {
        .product__excerpt p,
        .product__profile-header h2 {
            color: $gold;
        }

        strong {
            @include underline('gold');
        }
    }

    &-pink {
        .product__excerpt p,
        .product__profile-header h2 {
            color: $pink;
        }

        strong {
            @include underline('pink');
        }
    }

    &-salmon {
        .product__excerpt p,
        .product__profile-header h2 {
            color: $salmon;
        }

        strong {
            @include underline('salmon');
        }
    }

    &-grey {
        .product__excerpt p,
        .product__profile-header h2 {
            color: $grey;
        }

        strong {
            @include underline('grey');
        }
    }
}

.product__profile-header {
  @extend %flex;
  @extend %flex-wrap;
  @extend %flex-align-center;
  @extend %flex-justify-space-between;

  .h4 {
    margin-bottom: 0;
  }
}

.product__stockists-find {
  margin: 2rem 0;
}

.product__profile-block {
  margin-bottom: 2rem;

  p:last-of-type {
    margin-bottom: 0;
  }
}

.content-block--product-excerpt {
  @extend %flex;
  @extend %flex-column;
  @extend %flex-center;
  margin: 0;
}

.product__stockists {
  @extend %ul-no-style;
}

.button.product__stockists-link {
  @extend %h5;
  text-transform: none;
}



