  margin-bottom: .5rem;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  top: 0 !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
  overflow-x: hidden;
}

@media print {
  body {
    display: block;
    height: auto;
    width: 100%;
  }

  html {
    height: auto;
  }

  .table__wrapper {
    overflow: visible;
  }

  .row,
  .r {
    display: block;
  }

  .container {
    width: 100%;
  }

  .header__inner {
    flex-wrap: wrap;
  }

  .header__nav {
    width: 100%;
  }

  .header__strapline {
    text-align: left;
  }

  .nav,
  .button,
  .footer__bottom {
    display: none;
  }

  .content {
    padding: 0;
    border: 0;
  }
}

.moveit {
  will-change: transform;
}




